.main-header__wrapper {
  width: $mobile-width;
  position: relative;
  z-index: 10;
  margin: 0 auto;
  padding: 10px 20px 0 0;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: $tablet-width;
    padding: 30px 30px 0 30px;
    display: flex;
    text-align: center;
    align-items: center;
    min-height: 50px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    padding: 72px 110px 0 110px;
    min-height: 59px;
  }
}

.main-header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  text-align: center;
  position: relative;
  align-items: center;
  box-sizing: border-box;
  width: 280px;
  padding-left: 20px;
  padding-bottom: 13px;

  @media (min-width: $tablet-width) {
    width: 174px;
    margin: 0;
    align-items: center;
    padding: 0;
    margin-right: 94px;
  }

  @media (min-width: $desktop-width) {
    width: 202px;
    margin-right: 541px;
  }
}
